<script>
import DatePicker from 'vue2-datepicker'

export default {
  name: 'NewReocrdModal',
  components: { DatePicker },
  props: {
    record: {
      type: Object,
      default: () => {
        return null
      }
    },
    open: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      selectedCommonTag: [],
      currentDropdownCount: 0,
      currentDropdown: '',
      dropdownItem: null,
      isCommonTagEdit: false,
      dropdownOpen: false,
      currentLast: 0,
      customeize: {
        visitOpt: 0,
        relationOpt: 0,
        analizeOpt: 0,
        recommandOpt: 0,
        serviceOpt: 0
      },
      selectedTag: {
        visit: [],
        relation: [],
        analize: [],
        recommand: [],
        service: []
      },
      removeCommonTag: [],
      commonTag: {
        visit: [
          {name: '潛在客戶'},
          {name: '前景看好'},
          {name: '脾氣不好'},
          {name: '住在內湖地區'},
          {name: '富二代小開'}
        ],
        relation: [
          {name: '潛在客戶'},
          {name: '前景看好'},
          {name: '脾氣不好'},
          {name: '住在內湖地區'},
          {name: '富二代小開'}
        ],
        recommand: [
          {name: '潛在客戶'},
          {name: '前景看好'},
          {name: '脾氣不好'},
          {name: '住在內湖地區'},
          {name: '富二代小開'}
        ],
        analize: [
          {name: '潛在客戶'},
          {name: '前景看好'},
          {name: '脾氣不好'},
          {name: '住在內湖地區'},
          {name: '富二代小開'}
        ],
        service: [
          {name: '潛在客戶'},
          {name: '前景看好'},
          {name: '脾氣不好'},
          {name: '住在內湖地區'},
          {name: '富二代小開'}
        ]
      },
      visitInit: [
        {name: '增員關係', default: true}, {name: 'OPP', default: true}, {name: '初次面談', default: true}
      ],
      relationInit: [
        {name: '產險', default: true}, {name: '車險預約單', default: true}, {name: '開戶', default: true}, {name: '信用卡', default: true}, {name: '房貸', default: true}, {name: '房貸訪問表', default: true}, {name: '易Call保', default: true}, {name: '贈送媒體', default: true}, {name: '觀念溝通', default: true}, {name: '轉介紹', default: true}, {name: '二代健保', default: true}, {name: '送禮', default: true}
      ],
      analizeInit: [
        {name: '保單健檢', default: true}, {name: '資料蒐集', default: true}, {name: '四象限', default: true}, {name: '帆船理論', default: true}, {name: '家庭保障', default: true}, {name: '醫療保障', default: true}, {name: '子女教育', default: true}, {name: '退休養老', default: true}, {name: '投資理財', default: true}, {name: '稅務規劃', default: true}
      ],
      recommandInit: [
        {name: '壽險', default: true}, {name: '醫療險', default: true}, {name: '儲蓄險', default: true}, {name: '意外險', default: true}, {name: '年金險', default: true}, {name: '投資險', default: true}, {name: '旅平險', default: true}
      ],
      serviceInit: [
        {name: '人工/自繳', default: true}, {name: '保費', default: true}, {name: '理賠', default: true}, {name: '滿期金/年金', default: true}, {name: '指定拜訪', default: true}, {name: '墊滾/催停', default: true}, {name: '電子單據', default: true}, {name: '保全', default: true}, {name: '保單活化', default: true}, {name: '行動服務', default: true}, {name: '保單簽收回條', default: true}
      ],
      visitOpt: [],
      relationOpt: [],
      analizeOpt: [],
      recommandOpt: [],
      serviceOpt: [],
      form: {
        data: '',
        visit: [
          'OPP'
        ],
        relation: [
          '房貸', '房貸訪問表'
        ],
        ...this.record
      }
    }
  },
  watch: {
    'open': 'syncRecord'
  },
  methods: {
    syncRecord () {
      if(this.open) this.init()
    },
    init () {
      this.visitOpt = Object.assign([], this.visitInit)
      this.relationOpt = Object.assign([], this.relationInit)
      this.analizeOpt = Object.assign([], this.analizeInit)
      this.recommandOpt = Object.assign([], this.recommandInit)
      this.serviceOpt = Object.assign([], this.serviceInit)

      this.customeize.visitOpt = 0
      this.customeize.relationOpt = 0
      this.customeize.analizeOpt = 0
      this.customeize.recommandOpt = 0
      this.customeize.serviceOpt = 0
      
      this.renderCommonTag('visit')
      this.renderCommonTag('relation')
      this.renderCommonTag('analize')
      this.renderCommonTag('recommand')
      this.renderCommonTag('service')
      
      this.form = {...window._.clone(this.record) || {
        note: '',
        title: '',
        tags: [],
        visited: true
      }}
      
    },
    renderCommonTag (type) {
      if (!this.record) return
      let opt = `${type}Opt`
      this.selectedTag[type] = this.record[type] || []
      if(this.record[type] && this.record[type].length) {
        this.record[type].forEach(r => {
          let i = this[opt].find(item => item.name === r)
          if(!i || i==-1) {
            this[opt].push({name: r})
            this.selectedTag[type].push(r)
            this.customeize[opt]++
          }
        })
      }
    },
    insertCommonTag () {
      let opt = `${this.currentDropdown}Opt`

      if(this.removeCommonTag.length) {
        this.removeCommonTag.forEach(rm => {
          let index = -1;
          this[opt].forEach((op, i) => { 
            if( op.name == rm.name ) index = i
          })
          
          if(index > -1) {
            this.customeize[opt]--
            this[opt].splice(index, 1)
          }
        })
      }

      this.selectedCommonTag.forEach(item => {
        let f = this[opt].find(op => op.name == item)
        if(!f) {
          this.selectedCommonTag.push(item)
          this[opt].push({name: item})
          this.selectedTag[this.currentDropdown].push(item)
          this.customeize[opt]++
        }
      })
      this.dropdownClose()
    },
    showDropdown(type, evt) {
      this.dropdownClose()
      const $this = this
      this.removeCommonTag = []
      this.currentDropdown = type
      if(this.customeize[`${type}Opt`] == 3 ) return
      else {
        this[`${type}Opt`].forEach((item) => {
          $this.commonTag.visit.forEach(it => {
            if(it.name === item.name) {
              $this.selectedCommonTag.push(item.name)
            }
          })
        })
        // debugger
        this.dropdownOpen = true
        this.dropdownItem = evt.target
        this.currentLast = 3 - (this.customeize[`${type}Opt`] - $this.selectedCommonTag.length)
        this.dropdownItem.parentElement.classList.add('show')
        this.dropdownItem.nextSibling.classList.add('show')
      }
    },
    commitTag (type, btn) {
      btn.edit = false  
      return false
    },
    hasCommonItem (tag) {
      return this.selectedCommonTag.indexOf(tag) > -1
    },
    toggleCommonSelect (btn) {
      let type = `${this.currentDropdown}Opt`
      let index = this.selectedCommonTag.indexOf(btn.name)
      if (index > -1) {
        let id = 0
        this.selectedCommonTag.splice(index, 1)
        this.currentDropdownCount--
        this.removeCommonTag.push(btn)
      } else if(this.selectedCommonTag.length < this.currentLast) {
          this.currentDropdownCount++
          this.selectedCommonTag.push(btn.name)
      } 
    },
    toggleSelect (type, btn) {
      if (!this.form.visited) return
      let index = this.selectedTag[type].indexOf(btn.name)
      if (index > -1) {
        this.selectedTag[type].splice(index, 1)
      } else {
        this.selectedTag[type].push(btn.name)
      }
    },
    hasItem (attr, name) {
      if(!this.selectedTag[attr]) return false
      return this.selectedTag[attr].find(n => n == name);
    },
    removeItem (type, i) {
      this.customeize[type]--
      this[type].splice(i, 1)
    },
    addItem (type) {
      this.customeize[type]++
      this[type].push({
        name: '',
        edit: true
      })
    },
    dropdownClose () {
      this.dropdownOpen = false
      this.selectedCommonTag = []
      this.currentDropdownCount = 0
      if(!this.dropdownItem) return
      this.dropdownItem.parentNode.classList.remove('show')
      this.dropdownItem.nextSibling.classList.remove('show')
    },
    submit () {
      this.$emit('submit', {
        ...this.selectedTag,
        ...this.form
      })
    }
  },
  mounted () {
    this.init()

    this.$el.addEventListener('click', e => {
      if (!window.$(e.target).hasClass('dropdown-menu') && !$(event.target).parents().hasClass('dropdown-menu')) {
        this.dropdownClose()
      }
    })
  }
}
</script>
